import { CMIOT_NO_EXIST, PLATFORM_NO_SUPPORT } from "./helpers/error-msgs";
import { getUuid } from "./helpers/tools";

class JSSdk {
  constructor() {
    window.CMIOT = {};

    const u = navigator.userAgent;
    const isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1;
    const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);

    this.platform = "";
    if (isAndroid) {
      this.platform = "Android";
    }
    if (isiOS) {
      this.platform = "IOS";
    }
  }

  doctor() {
    switch (this.platform) {
      case "Android":
        return !!window.cmiot;
      case "IOS":
        return (
          !!window.webkit &&
          !!window.webkit.messageHandlers &&
          !!window.webkit.messageHandlers.cmiot &&
          !!window.webkit.messageHandlers.cmiot.postMessage
        );
      default:
        return false;
    }
  }

  dealResult(result) {
    switch (this.platform) {
      case "Android":
        return JSON.parse(result);
      case "IOS":
        return result;
      default:
        return null;
    }
  }

  action({
    method = "",
    success = "",
    fail = "",
    complete = "",
    callback = "",
    parameters = {},
  }) {
    const _parameters = {};
    method && (_parameters.method = method);
    success && (_parameters.success = success);
    fail && (_parameters.fail = fail);
    complete && (_parameters.complete = complete);
    callback && (_parameters.callback = callback);

    switch (this.platform) {
      case "Android":
        cmiot[method](JSON.stringify({ ..._parameters, ...parameters }));
        break;
      case "IOS":
        _parameters.parameters = parameters;
        window.webkit.messageHandlers.cmiot.postMessage(_parameters);
        break;
      default:
        break;
    }
  }

  createAsync({
    method,
    success = () => {},
    fail = () => {},
    complete = () => {},
    parameters = {},
  }) {
    if (!this.doctor()) {
      fail(CMIOT_NO_EXIST);
      return;
    }

    if (!this.canIUse(method)) {
      fail(PLATFORM_NO_SUPPORT);
      return;
    }

    const _success = `${method}_success_${getUuid()}`;
    const _fail = `${method}_fail_${getUuid()}`;
    const _complete = `${method}_complete_${getUuid()}`;
    window.CMIOT[_success] = (result) => success(this.dealResult(result));
    window.CMIOT[_fail] = (err) => fail(this.dealResult(err));
    window.CMIOT[_complete] = (result) => complete(this.dealResult(result));

    this.action({
      method,
      parameters,
      success: `CMIOT.${_success}`,
      fail: `CMIOT.${_fail}`,
      complete: `CMIOT.${_complete}`,
    });
  }

  createAsyncListener({ method, callback = () => {} }) {
    if (!this.doctor()) {
      callback(CMIOT_NO_EXIST);
      return;
    }

    if (!this.canIUse(method)) {
      fail(PLATFORM_NO_SUPPORT);
      return;
    }

    const _callback = `${method}_callback_${getUuid()}`;
    window.CMIOT[_callback] = (result) => callback(this.dealResult(result));
    this.action({
      method,
      callback: `CMIOT.${_callback}`,
    });
  }

  canIUse(method) {
    if (!this.doctor()) {
      return false;
    }

    switch (this.platform) {
      case "Android":
        return cmiot.canIUse(method);
      case "IOS":
        return Number(window.prompt("canIUse", method)) === 0 ? false : true;
      default:
        return false;
    }
  }

  getSystemInfo() {
    if (!this.doctor()) {
      return null;
    }

    switch (this.platform) {
      case "Android":
        return JSON.parse(cmiot.getSystemInfo());
      case "IOS":
        return JSON.parse(window.prompt("getSystemInfo"));
      default:
        return null;
    }
  }

  getNetworkType() {
    if (!this.doctor()) {
      return null;
    }

    switch (this.platform) {
      case "Android":
        return cmiot.getNetworkType();
      case "IOS":
        return window.prompt("getNetworkType");
      default:
        return null;
    }
  }

  onNetworkStatusChange(callback) {
    this.createAsyncListener({ method: "onNetworkStatusChange", callback });
  }

  offNetworkStatusChange() {
    if (!this.doctor()) {
      return null;
    }

    switch (this.platform) {
      case "Android":
        return cmiot.offNetworkStatusChange();
      case "IOS":
        return window.prompt("offNetworkStatusChange");
      default:
        return null;
    }
  }

  getConnectedWifi({ success, fail, complete }) {
    this.createAsync({ method: "getConnectedWifi", success, fail, complete });
  }

  startWifi({ success, fail, complete }) {
    this.createAsync({ method: "startWifi", success, fail, complete });
  }

  stopWifi({ success, fail, complete }) {
    this.createAsync({ method: "stopWifi", success, fail, complete });
  }

  getWifiList({ success, fail, complete }) {
    this.createAsync({ method: "getWifiList", success, fail, complete });
  }

  connectWifi({ SSID, BSSID = "", password, success, fail, complete }) {
    this.createAsync({
      method: "connectWifi",
      success,
      fail,
      complete,
      parameters: {
        SSID,
        BSSID,
        password,
      },
    });
  }

  scanCode({ success, fail, complete }) {
    this.createAsync({ method: "scanCode", success, fail, complete });
  }

  openBluetoothAdapter({ success, fail, complete }) {
    this.createAsync({
      method: "openBluetoothAdapter",
      success,
      fail,
      complete,
    });
  }

  closeBluetoothAdapter({ success, fail, complete }) {
    this.createAsync({
      method: "closeBluetoothAdapter",
      success,
      fail,
      complete,
    });
  }

  onBluetoothDeviceFound(callback) {
    this.createAsyncListener({ method: "onBluetoothDeviceFound", callback });
  }
  offBluetoothDeviceFound() {
    if (!this.doctor()) {
      return null;
    }

    switch (this.platform) {
      case "Android":
        return cmiot.offBluetoothDeviceFound();
      case "IOS":
        return window.prompt("offBluetoothDeviceFound");
      default:
        return null;
    }
  }
  getBluetoothDevices({ success, fail, complete }) {
    this.createAsync({
      method: "getBluetoothDevices",
      success,
      fail,
      complete,
    });
  }

  navigateTo({
    url,
    customHeader = false,
    title = "Title",
    success,
    fail,
    complete,
  }) {
    this.createAsync({
      method: "navigateTo",
      success,
      fail,
      complete,
      parameters: {
        url,
        customHeader,
        title,
      },
    });
  }
}

export default new JSSdk();
