import jssdk from "./jssdk";

document.getElementById("button-can-i-use-scan-code").onclick = () => {
  const b = jssdk.canIUse("scanCode");
  console.log(b);
};
document.getElementById("button-get-systeminfo").onclick = () => {
  const b = jssdk.getSystemInfo();
  console.log(b);
};
document.getElementById("button-get-network-type").onclick = () => {
  const type = jssdk.getNetworkType();
  console.log(type);
};
document.getElementById("button-on-network-status-change").onclick = () => {
  jssdk.onNetworkStatusChange((result) => {
    console.log(result);
  });
};
document.getElementById("button-off-network-status-change").onclick = () => {
  jssdk.offNetworkStatusChange();
};
document.getElementById("button-get-connected-wifi").onclick = () => {
  jssdk.getConnectedWifi({
    success: (res) => console.log("success", res),
    fail: (err) => console.error("error", err),
    complete: (res) => {
      if (res.resultCode === "000000") {
        console.log("complete", res);
      }
    },
  });
};
